<template>
  <div class="shopmanage">
    <h1 class="tatle"></h1>

    <div>
      <el-form size="medium" :disabled="isDisabled" ref="form" :model="form" label-width="80px">

        <!--  -->
        <el-form-item label="">
          <el-input v-model="form.shopname"></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-input v-model="form.address"></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-input v-model="form.shopdesc"></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-input v-model="form.slogan"></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-select v-model="form.category" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-checkbox-group v-model="form.feature">
            <el-checkbox label=" " name=""></el-checkbox>
            <el-checkbox label="" name=""></el-checkbox>
            <el-checkbox label="" name=""></el-checkbox>
            <el-checkbox label="" name=""></el-checkbox>
            <br />
            <el-checkbox label="" name=""></el-checkbox>
            <el-checkbox label="" name=""></el-checkbox>
          </el-checkbox-group>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-input-number v-model="form.packingexpense" :min="1" :max="30" label=""></el-input-number>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-input-number v-model="form.minprice" :min="1" :max="30" label=""></el-input-number>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-time-picker
            is-range
            v-model="form.date"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            placeholder=""
            value-format="HH:mm:ss"
          ></el-time-picker>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-upload
            class="avatar-uploader"
            :action="uploadImgApi"
            :show-file-list="false"
            :on-success="handleAvatarSuccess1"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if=" form.dialogImageUrl1" :src="serveApi + form.dialogImageUrl1" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <!--  -->
        <el-form-item label="">
          <el-upload
            class="avatar-uploader"
            :action="uploadImgApi"
            :show-file-list="false"
            :on-success="handleAvatarSuccess2"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if=" form.dialogImageUrl2" :src="serveApi + form.dialogImageUrl2" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <!--   -->
        <el-form-item label=" ">
          <el-upload
            class="avatar-uploader"
            :action="uploadImgApi"
            :show-file-list="false"
            :on-success="handleAvatarSuccess3"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if=" form.dialogImageUrl3" :src="serveApi + form.dialogImageUrl3" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-select @change="specialOfferadd" v-model="form.specialOffer" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>

        <!--   -->
        <el-table :data="form.tableData" border style="width:642px">
          <el-table-column prop="title" label="" width="180"></el-table-column>
          <el-table-column prop="name" label="" width="180"></el-table-column>
          <el-table-column prop="detail" label="" width="180"></el-table-column>
          <el-table-column @click="dialogFormVisible=true" label="" width="100">
            <template slot-scope="scope">
              <el-button
                @click="deleteTableCon(scope.$index)"
                size="mini"
                plain
                type="danger"
                icon="el-icon-delete"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-form>

      <el-button @click="modify" type="primary"></el-button>
      <el-button @click="Preservation" type="primary"></el-button>
    </div>

    <!--     -->
    <el-dialog width="660px" title="" :visible.sync="dialogFormVisible">
      <el-form :model="Addfrom" :rules="rules" ref="Addfrom" style="width: 337px;">
        <el-form-item label="" label-width="140px" prop="detail">
          <el-input v-model="Addfrom.detail" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogForm"> </el-button>
        <el-button @click="addDetail" type="primary"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
//
import { shopinfo, shopedit } from "../api/shop";

export default {
  data() {
    return {
      isDisabled: false, // 
      serveApi: "http://127.0.0.1:8088/upload/shop/", //   
      uploadImgApi: "http://127.0.0.1:8088/shop/upload/",// 
      rules: {
        detail: [{ required: true, message: "", trigger: "blur" }]
      },
      /*  */
      form: {
        shopname: "", //
        address: "", //
        phone: "", //
        shopdesc: "", //
        slogan: "", //
        category: "", //
        feature: [], //
        packingexpense: 0, //
        minprice: 20, //
        date: [], //
        dialogImageUrl1: "", //
        dialogImageUrl2: "", //
        dialogImageUrl3: "", //
        specialOffer: "", //
        // 
        tableData: []
      },
      /*  */
      Addfrom: {
        detail: ""
      },
      dialogFormVisible: false //
    };
  },

  methods: {
    /*     */
    handleAvatarSuccess1(res, file) {
      console.log(res); // 
      let { code, imgUrl } = res;
      if (code === 0) {
        this.form.dialogImageUrl1 = imgUrl;
      }
    },
    handleAvatarSuccess2(res, file) {
      console.log(res); // 
      let { code, imgUrl } = res;
      if (code === 0) {
        this.form.dialogImageUrl2 = imgUrl;
      }
    },
    handleAvatarSuccess3(res, file) {
      console.log(res); // 
      let { code, imgUrl } = res;
      if (code === 0) {
        this.form.dialogImageUrl3 = imgUrl;
      }
    },
    /*    */
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isPNG = file.type === "image/png";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!(isJPG || isPNG)) {
        this.$message.error(" JPGPNG !");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isJPG && isLt2M;
    },
    /*   */
    deleteTableCon(index) {
      this.form.tableData.splice(index, 1);
    },
    /*  */
    specialOfferadd() {
      this.dialogFormVisible = true;
    },
    /*  */
    dialogForm() {
      this.dialogFormVisible = false;
      this.Addfrom = { detail: "" };
    },
    /*  */
    addDetail() {
      this.$refs.Addfrom.validate(valid => {
        if (valid) {
          this.dialogFormVisible = false;
          let title;
          //   title
          switch (this.form.specialOffer) {
            case "":
              title = "";
              break;
            case "":
              title = "";
              break;
            case "":
              title = "";
              break;
            case "":
              title = "";
              break;
          }

          // 
          let newObj = {
            title,
            name: this.form.specialOffer,
            detail: this.Addfrom.detail
          };

          // 
          this.form.tableData.unshift(newObj);
          this.Addfrom = { detail: "" };
        } else {
          this.Addfrom = { detail: "" };
          return;
        }
      });
    },
    /*  */
    async Preservation() {
      this.isDisabled = true;

      /*  */

      let {
        shopname, //
        address, //
        phone, //
        shopdesc, //
        slogan, //
        category, //
        feature, //
        packingexpense, //
        minprice, //
        date, //
        dialogImageUrl1, //
        dialogImageUrl2, //
        dialogImageUrl3, //
        specialOffer, //
        tableData // 
      } = this.form;

      console.log(
        shopname, //
        address, //
        phone, //
        shopdesc, //
        slogan, //
        category, //
        feature, //
        packingexpense, //
        minprice, //
        date, //
        dialogImageUrl1, //
        dialogImageUrl2, //
        dialogImageUrl3, //
        specialOffer, //
        tableData // 
      );
      console.log(this.form);

      /*  */
      let params = {
        shopname,
        address,
        phone,
        shopdesc,
        slogan,
        category,
        feature: JSON.stringify(feature),
        packingexpense,
        minprice,
        date: JSON.stringify(date),
        shopAvatar: dialogImageUrl1, // 
        businessLicenseImg: dialogImageUrl2, // 
        cateringServiceLicenseImg: dialogImageUrl3, // 
        tableData: JSON.stringify(tableData)
      };

      /* ajax */
      await shopedit(params);
    },
    // 
    modify() {
      this.isDisabled = false;
    },
    /*   */
    async getShopInfo() {
      let { data } = await shopinfo();
      console.log(data);
      for (let key in data) {
        console.log(key);
        console.log(data[key]);
        //   
        if (key === "date" || key === "feature" || key === "tableData") {
          data[key] = JSON.parse(data[key]);
        }
      }
      // 
      this.form = { ...data };

      //
      this.form.dialogImageUrl1 = data.shopAvatar;
      this.form.dialogImageUrl2 = data.businessLicenseImg;
      this.form.dialogImageUrl3 = data.cateringServiceLicenseImg;
    }
  },
  /*  */
  created() {
    this.getShopInfo();
  }
};
</script>

<style lang="less" scoped>
.shopmanage {
  min-height: 500px;
  background: #fff;
  box-shadow: 0 0 10px 3px #f1f1f1;
  // padding-top: 30px;
  padding: 30px 0 0 40px;
  .tatle {
    margin: 0 0 30px 30px;
    font-size: 18px;
    color: #333;
    font-family: 600;
  }

  .el-input {
    width: 400px;
    height: 20px;
  }

  .input {
    width: 400px;
  }

  .table-box {
    margin-left: 100px;
  }
  .descride-bottom {
    margin-top: 20px;
    margin-right: 30px;
  }
  .standard-inp {
    width: 180px;
  }
  .avatar-uploader {
    width: 178px;
    height: 178px;
    border: 1px dashed #ccc;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader:hover {
    border-color: #409eff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .from-item {
    // margin-top: 30px;
    margin: 30px 0;
  }
}
</style>